<template>
	<div class="body">
		<div class="body_header flex_start">
			<i class="icon iconfont icon-gongsiyunhang1"></i>
			<span>{{ name }}</span>
		</div>
		<div class="body_title">
			<i class="icon iconfont icon-gongsiyunhang2"></i>
			<span>公司运行</span>
		</div>
		<div class="fund_run_nav flex_start">
			<div v-for="(item,index) of navList" :key="index" class="fund_run_nav_list">
				<router-link :to="'/home/fundManageHome/manageCompany/manageCompanyRun/'+item.router+'?id='+id">
					{{item.name}}
				</router-link>
			</div>
		</div>
		<div class="company_run_main">
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	import { getNameApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	
	export default {
		name: 'manageCompanyRun',
		data() {
			return {
				id: '',
				name: '',
				navList: [ 
					{
						name: '在管基金',
						step: 1,
						router: 'underManageFund',
					}, 
					{
						name: '投资组合',
						step: 2,
						router: 'investGroup',
					}, 
					{
						name: '现金流',
						step: 3,
						router: 'cashFlow',
					}, 
				],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getName()
		},
		mounted() {},
		components: {},
		methods: {
			getName(){
				getNameApi({
					id: this.id,
					type: 3
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.name = res.data.payload.data.name;
					} else {
						dialogConfirm('info', res.data.message);
					}
				})
			},
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 25px;
	}
	.body_title{
		padding-bottom: 20px;
		font-size: 16px;
	}
	.fund_run_nav{
		margin-top: 5px;
		border-top: 1px solid #F1F1F1;
		border-bottom: 1px solid #F1F1F1;
	}
	.fund_run_nav_list{
		height: 67px;
		padding: 0 25px;
		border-bottom: 2px solid white;
	}
	.fund_run_nav_list:nth-child(1){
        padding-left: 0px;
    }
	.fund_run_nav_list a{
		display: block;
		height: 67px;
		line-height: 67px;
		box-sizing: border-box;
		/* padding: 0 3px; */
		font-size: 14px;
	}
	.fund_run_nav_list .router-link-active{
		color: #6292F4 !important;
		background: white border-box;
		border-bottom: 2px solid #6292F4;
	}
</style>


